.blog{
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    h4{
        font-size: 4vw;
        margin-bottom: 80vh;
    }
    .arrow{
        height: 8vh;
        position: absolute;
        cursor: pointer;
        &.left{
            left: 100px;
            transform: rotate(90deg);
        }
        &.right{
            right: 100px;
            transform: rotate(-90deg);
        }
    }

    h1{
        padding: 10vh;
        color: black;
        font-size: 10vw;
    }
    
    .slider{
        height: 60vh;
        display: flex;
        position:absolute;
        left: 0;
        transition: all 1s ease-out;
    }

    .container{
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;

        .item{
            color: white;
            width: 50%;
            height: 100%;
            background-color: black;
            border-radius: 10%;
            display: flex;
            align-items: center;
            justify-content: center;

            .left{
                flex: 4;
                height: 80%;
                display: flex;
                align-items: center;
                justify-content: center;

                .leftcontainer{
                    width: 90%;
                    height: 70%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;

                    .imgcontainer{
                        width: 3vw;
                        height: 5vh;
                        border-radius: 50%;
                        background-color: white;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        img{
                            width: 5vw;
                        }
                    }
                    h2{
                        font-size: 1.5vw;
                    }
                    p{
                        font-size: .8vw;
                    }
                    span{
                        font-size: .7vw;
                        font-weight: 500;
                        color: white;
                        text-decoration: none;
                        a{
                            color: white;
                            :hover{
                                color: cadetblue;
                            }
                        }
                    }
                }
            }
            .right{
                background-color: white;
                flex: 4;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                overflow: hidden;

                img{
                    width: 200vw;
                }
            }
        }
    }
} 