.portfolio{
    background-color: black;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;

    a{
        text-decoration: none;
        color: white;
    }

    h1{
        padding: 7vh;
        font-size: 5vw;
    }

    ul{
        margin: 10px;
        padding: 10px;
        list-style: none;
        display: flex;

    }
    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        
        .item{
            width: 14vw;
            height: 18vh;
            border-radius: 10px;
            border: 1px solid white;
            margin: 15px 20px;
            align-items: center;
            justify-content: center;
            display: flex;
            position: relative;

            h3{
                position: absolute;
                font-size: 20px;
            }
            img{
                height: 100%;
                width: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: cadetblue;
                img{
                    opacity: .2;
                    z-index: 0;
                }
            }
        }
    }
} 