.contact {
  background-color: black;
  color: white;
  display: flex;

  .left {
    flex: 1;
    overflow: hidden;

    img {
      height: 100%;
    }
  }
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h2 {
      font-size: 30px;
    }

    form {
      width: 70%;
      height: 70%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;

      input {
        width: 20vw;
        padding: 12px 20px;
        margin: 8px 0;

      }

      textarea {
        width: 20vw;
        height: 200px;
        font-size: 14px;
        padding: 10px;
        max-width: 100%;
        line-height: 1.5;
        border-radius: 5px;
        border: 1px solid #ccc;
        box-shadow: 1px 1px 1px #999;
      }

      button {
        width: 150px;
        height: 30px;
        color: white;
        background-color: cadetblue;
        border: none;
        border-radius: 10px;
        font-weight: 500;
        cursor: pointer;

        &:focus {
          outline: none;
        }
      }

      span {
        color: cadetblue;
      }
    }
  }
}